div.ui-toggle {
  margin: 0;
  padding: 0;
}
div.ui-toggle input[type=checkbox] {
  display: none;
}
div.ui-toggle input[type=checkbox]:checked + label {
  border-color: #0073aa;
  background-color: #0073aa;
  box-shadow: inset 0 0 0 height/2;
}
div.ui-toggle input[type=checkbox]:checked + label > div {
  margin-left: 16px;
}
div.ui-toggle label {
  transition: all 0.2s ease;
  display: inline-block;
  position: relative;
  user-select: none;
  -moz-user-select: none;
  background-color: #374140;
  box-shadow: inset 0 0 0 0 #0073aa;
  border: 2px solid #374140;
  border-radius: 22px;
  width: 40px;
  height: 20px;
}
div.ui-toggle label div {
  transition: all 200ms ease;
  background-color: #fff;
  width: 20px;
  height: 20px;
  border-radius: 10px;
}
div.ui-toggle label:hover, div.ui-toggle label > div:hover {
  cursor: pointer;
}
div.ui-toggle.mb-10 {
  margin-bottom: 10px;
}

.nav-tabs {
  float: left;
  width: 100%;
  margin: 0;
  list-style-type: none;
  border-bottom: 1px solid transparent;
}
.nav-tabs > li {
  float: left;
  margin-bottom: -1px;
}
.nav-tabs > li > a {
  margin-right: 2px;
  line-height: 1.5;
  padding: 10px;
  border: 1px solid transparent;
  border-radius: 4px 4px 0 0;
  float: left;
  text-decoration: none;
}
.nav-tabs > li > a:hover {
  border-color: #eee #eee #ddd;
}
.nav-tabs > li.active > a, .nav-tabs > li.active > a:hover, .nav-tabs > li.active > a:focus {
  color: #555;
  cursor: default;
  background-color: #fff;
  border-color: transparent;
}

.tab-content > .tab-pane {
  float: left;
  width: 100%;
  display: none;
}
.tab-content > .tab-pane.active {
  display: block;
  padding: 10px;
  background-color: #fff;
  box-shadow: 0 5px 4px -2px rgba(0, 0, 0, 0.15);
}

.wrap.upsell-manager .wrap.upsell-products-container * {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.wrap.upsell-manager .categories-selected {
  margin-bottom: 2rem !important;
  font-size: 2rem;
  text-decoration: underline;
}
.wrap.upsell-manager .hidden {
  display: none;
}
.wrap.upsell-manager .container {
  padding: 2rem !important;
  margin-bottom: 3rem !important;
}
.wrap.upsell-manager .products-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
}
.wrap.upsell-manager .product-container {
  width: 250px;
  height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 7rem !important;
}
.wrap.upsell-manager .product-container .product-image {
  width: 100%;
  height: 100%;
  position: relative;
  margin-bottom: 0.5rem !important;
}
.wrap.upsell-manager .product-container .product-image input[type=hidden] {
  display: none;
}
.wrap.upsell-manager .product-container .product-image img {
  width: 100%;
  height: 100%;
}
.wrap.upsell-manager .product-container .product-image input[type=checkbox] {
  position: absolute;
  width: 100%;
  height: 100%;
  background: none;
  cursor: pointer;
}
.wrap.upsell-manager .product-container .product-image input[type=checkbox]:checked {
  content: "";
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  border: 2px solid blue;
}
.wrap.upsell-manager .product-container .product-name {
  font-size: 1.2rem;
}
.wrap.upsell-manager .product-container .produce-price {
  font-size: 1.2rem;
}
.wrap.upsell-manager .button.button-primary {
  padding: 0.5rem;
}

.wrap.upsell-manager-options .iucp-setting-container.color .toggle-background {
  margin-bottom: 1rem;
}
.wrap.upsell-manager-options .iucp-setting-container.color .toggle-background label {
  margin-right: 1rem;
}
.wrap.upsell-manager-options .iucp-setting-container.color .toggle-background p {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-right: 1rem;
}
.wrap.upsell-manager-options .iucp-flex {
  display: flex;
  align-items: center;
  gap: 2rem;
}